package mzx.ventas.data.repository

import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.sync.Mutex
import kotlinx.coroutines.sync.withLock
import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.service.CartService

interface CartRepository {
    suspend fun addProductToCart(productItemElement: ProductItemElement)

    fun cartState(): Flow<CartState>

    fun getCartState(): CartState

    suspend fun removeProductFromCart(productItemElement: ProductItemElement)
}

class CartRepositoryImpl(private val cartService: CartService) : CartRepository {

    private var cart = cartService.fetchCartState()
    private val mutex = Mutex()

    private val _cartState = MutableStateFlow(cart)

    override suspend fun addProductToCart(productItemElement: ProductItemElement) {
        mutex.withLock {
            cart = cart.addProduct(productItemElement)
            cart
        }.let {
            _cartState.value = it
            cartService.updateCartState(it)
        }
    }

    override fun cartState(): Flow<CartState> = _cartState.asStateFlow()

    override fun getCartState(): CartState = cart
    override suspend fun removeProductFromCart(productItemElement: ProductItemElement) {
        mutex.withLock {
            cart = cart.removeProduct(productItemElement)
            cart
        }.let {
            cartService.updateCartState(it)
            _cartState.value = it
        }
    }
}

private fun CartState.removeProduct(productItemElement: ProductItemElement): CartState {
    val newProducts = products[productItemElement.id]?.let { count ->
        if (count == 1) {
            console.log("Removed ${products[productItemElement.id]} ")

            products.filterNot { (key, _) ->
                key == productItemElement.id
            }
        } else {
            products.entries.associate { entry ->
                entry.key to if (entry.key == productItemElement.id) {
                    entry.value - 1
                } else {
                    entry.value
                }
            }.also { console.log("decrease ${it}") }
        }
    }.also {
        console.log("Test: $it")
    }?:products
    return copy(products =  newProducts)

}

data class CartState(val products: Map<String, Int>) {
    companion object {
        val EMPTY = CartState(emptyMap())
    }
}

private fun CartState.addProduct(productItemElement: ProductItemElement): CartState {
    val count = (this.products[productItemElement.id] ?: 0) + 1
    console.log("Old Print $this")
    return copy(products = products + (productItemElement.id to count)).also {
        console.log("New Print $this")
    }
}
