package mzx.ventas.domain.usecase

import arrow.core.Either
import arrow.core.right
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.FlowCollector
import kotlinx.coroutines.flow.flow
import mzx.ventas.data.element.ProductItemElement
import mzx.ventas.data.repository.CartRepository
import mzx.ventas.data.repository.ProductsRepository

interface GetCartProductsUseCase {

    suspend fun action(): Flow<Either<Error, List<CartItemElement>>>
}

suspend fun FlowCollector<Either<Error, List<CartItemElement>>>.publishOnFlow(
    cartItemElements: List<CartItemElement>
) {
    emit(cartItemElements.right())
}

class GetCartProductsUseCaseImpl(
    private val cartRepository: CartRepository,
    private val productsRepository: ProductsRepository
) : GetCartProductsUseCase {
    override suspend fun action(): Flow<Either<Error, List<CartItemElement>>> = flow {
        val productElementsMap = productsRepository.fetchProducts()
            .associateBy { productItemElement -> productItemElement.id }

        cartRepository.cartState().collect { cartState ->
            cartState.products.entries.map(productElementsMap::map).letSuspend(::publishOnFlow)
        }
    }
}


private fun Map<String, ProductItemElement>.map(cartItem: Map.Entry<String, Int>): CartItemElement {
    val (id, count) = cartItem
    return CartItemElement(
        product = this[id] ?: throw IllegalArgumentException("Element doesn't exist - $id  "),
        count
    )
}

data class CartItemElement(
    val product: ProductItemElement,
    val quantity: Int,
    val amount: Double? = null  // For weight/volume-based items (kg, liters, etc.))
)


// through the existing suspend context.
suspend inline fun <T, R> T.letSuspend(block: suspend (T) -> R): R {
    return block(this) // The compiler handles the suspension under the hood
}