// Extension function to format Double to fixed decimal places in Kotlin/JS
fun Double.toFixed(decimals: Int): String {
    return this.asDynamic().toFixed(decimals) as String
}

val CartContainerStyle = CssStyle {
    base {
        Modifier
            .fillMaxWidth()
            .gap(1.5.cssRem)
    }
}

val CartItemStyle = CssStyle {
    base {
        Modifier
            .fillMaxWidth()
            .padding(1.cssRem)
            .borderRadius(0.75.cssRem)
            .backgroundColor(rgba(0, 0, 0, 0.02))
            .display(DisplayStyle.Grid)
            .gridTemplateColumns { size(80.px); size(1.fr) }
            .gap(1.cssRem)
    }
    Breakpoint.MD {
        Modifier.gridTemplateColumns {
            size(100.px)
            size(1.fr)
            size(200.px)
        }
    }
}

@Composable
fun CartDetail() {
    val ctx = rememberPageContext()

    val viewModel = remember { GlobalContext.get().get<CartViewModel>() }
    val onEvent = viewModel::onEvent
    val state by viewModel.state.collectAsState()

    LaunchedEffect(Unit) {
        onEvent(CartUiEvent.Init)
    }

    DisposableEffect(Unit) {
        onDispose {
            viewModel.onDispose()
        }
    }

    Column(
        modifier = Modifier.fillMaxWidth().gap(1.cssRem),
        horizontalAlignment = Alignment.Start
    ) {
        // Back Button
        Row(
            modifier = Modifier
                .cursor(Cursor.Pointer)
                .onClick { ctx.router.tryRoutingTo("/") }
                .gap(0.5.cssRem)
                .padding(0.5.cssRem),
            verticalAlignment = Alignment.CenterVertically
        ) {
            FaArrowLeft(
                modifier = Modifier
                    .fontSize(1.cssRem)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
            SpanText(
                "Volver a Productos",
                Modifier
                    .fontSize(1.cssRem)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
        }

        // Title
        SpanText(
            "Carrito de Compras",
            Modifier
                .fontSize(2.5.cssRem)
                .fontWeight(FontWeight.Bold)
                .color(ColorMode.current.toSitePalette().brand.primary)
        )

        // Cart Content
        if (state.isEmpty) {
            EmptyCartContent(ctx)
        } else {
            CartContent(state, onEvent, ctx)
        }
    }
}

@Composable
private fun EmptyCartContent(ctx: com.varabyte.kobweb.core.PageContext) {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(4.cssRem, 2.cssRem)
            .gap(1.5.cssRem),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        FaCartShopping(
            modifier = Modifier
                .fontSize(5.cssRem)
                .color(rgba(100, 100, 100, 0.3))
        )

        SpanText(
            "Tu carrito est vaco",
            Modifier
                .fontSize(1.5.cssRem)
                .fontWeight(FontWeight.Bold)
                .color(ColorMode.current.toSitePalette().brand.primary)
        )

        SpanText(
            "Agrega productos para comenzar tu compra",
            Modifier
                .fontSize(1.cssRem)
                .color(rgba(100, 100, 100, 0.8))
        )

        Box(
            modifier = Modifier
                .padding(1.2.cssRem, 0.75.cssRem)
                .borderRadius(0.75.cssRem)
                .backgroundColor(ColorMode.current.toSitePalette().brand.accent)
                .cursor(Cursor.Pointer)
                .onClick { ctx.router.tryRoutingTo("/") }
        ) {
            SpanText(
                "Ver Productos",
                Modifier
                    .fontSize(1.1.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(rgb(255, 255, 255))
            )
        }
    }
}

@Composable
private fun CartContent(
    state: CartUiState,
    onEvent: (CartUiEvent) -> Unit,
    ctx: com.varabyte.kobweb.core.PageContext
) {
    Column(
        modifier = Modifier.fillMaxWidth().gap(2.cssRem)
    ) {
        // Cart Items List
        Column(
            modifier = CartContainerStyle.toModifier(),
            horizontalAlignment = Alignment.Start
        ) {
            state.items.forEach { item ->
                CartItemCard(item, onEvent)
            }
        }

        // Cart Summary
        CartSummary(state, onEvent, ctx)
    }
}

@Composable
private fun CartItemCard(
    item: CartItem,
    onEvent: (CartUiEvent) -> Unit
) {
    Div(CartItemStyle.toModifier().toAttrs()) {
        // Product Image
        Image(
            src = item.product.imageUrl,
            modifier = Modifier
                .fillMaxWidth()
                .height(80.px)
                .borderRadius(0.5.cssRem)
                .objectFit(ObjectFit.Cover)
        )

        // Product Details
        Column(
            modifier = Modifier.fillMaxWidth().gap(0.5.cssRem),
            horizontalAlignment = Alignment.Start
        ) {
            SpanText(
                item.product.name,
                Modifier
                    .fontSize(1.2.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )

            SpanText(
                "$${item.product.price}",
                Modifier
                    .fontSize(1.cssRem)
                    .fontWeight(FontWeight.SemiBold)
                    .color(ColorMode.current.toSitePalette().brand.accent)
            )

            // Quantity Controls (visible on small screens)
            Row(
                modifier = Modifier
                    .gap(0.5.cssRem)
                    .display(DisplayStyle.Flex),
                verticalAlignment = Alignment.CenterVertically
            ) {
                SpanText(
                    "Cantidad:",
                    Modifier
                        .fontSize(0.9.cssRem)
                        .color(rgba(100, 100, 100, 0.9))
                )
                QuantityControls(item, onEvent, isCompact = true)
            }
        }

        // Action Column (visible on medium+ screens)
        Column(
            modifier = Modifier
                .fillMaxWidth()
                .gap(0.75.cssRem)
                .display(DisplayStyle.None),
            horizontalAlignment = Alignment.End
        ) {
            // Subtotal
            SpanText(
                "$${item.subtotal.toFixed(2)}",
                Modifier
                    .fontSize(1.3.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )

            // Quantity Controls
            QuantityControls(item, onEvent, isCompact = false)

            // Remove Button
            Box(
                modifier = Modifier
                    .padding(0.5.cssRem, 0.4.cssRem)
                    .borderRadius(0.5.cssRem)
                    .backgroundColor(rgba(220, 53, 69, 0.1))
                    .cursor(Cursor.Pointer)
                    .onClick { onEvent(CartUiEvent.RemoveItem(item.product)) }
            ) {
                Row(
                    modifier = Modifier.gap(0.4.cssRem),
                    verticalAlignment = Alignment.CenterVertically
                ) {
                    FaTrash(
                        modifier = Modifier
                            .fontSize(0.9.cssRem)
                            .color(rgb(220, 53, 69))
                    )
                    SpanText(
                        "Eliminar",
                        Modifier
                            .fontSize(0.9.cssRem)
                            .fontWeight(FontWeight.Medium)
                            .color(rgb(220, 53, 69))
                    )
                }
            }
        }
    }
}

@Composable
private fun QuantityControls(
    item: CartItem,
    onEvent: (CartUiEvent) -> Unit,
    isCompact: Boolean
) {
    Row(
        modifier = Modifier.gap(0.5.cssRem),
        verticalAlignment = Alignment.CenterVertically
    ) {
        // Decrement Button
        Box(
            modifier = Modifier
                .size(if (isCompact) 30.px else 35.px)
                .borderRadius(0.4.cssRem)
                .backgroundColor(ColorMode.current.toSitePalette().brand.primary)
                .cursor(Cursor.Pointer)
                .onClick { onEvent(CartUiEvent.DecrementQuantity(item.product)) },
            contentAlignment = Alignment.Center
        ) {
            FaMinus(
                modifier = Modifier
                    .fontSize(if (isCompact) 0.7.cssRem else 0.9.cssRem)
                    .color(rgb(255, 255, 255))
            )
        }

        // Quantity Display
        Box(
            modifier = Modifier
                .minWidth(if (isCompact) 40.px else 50.px)
                .padding(if (isCompact) 0.4.cssRem else 0.5.cssRem)
                .borderRadius(0.4.cssRem)
                .backgroundColor(rgba(0, 0, 0, 0.05)),
            contentAlignment = Alignment.Center
        ) {
            SpanText(
                "${item.quantity}",
                Modifier
                    .fontSize(if (isCompact) 0.9.cssRem else 1.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
        }

        // Increment Button
        Box(
            modifier = Modifier
                .size(if (isCompact) 30.px else 35.px)
                .borderRadius(0.4.cssRem)
                .backgroundColor(ColorMode.current.toSitePalette().brand.primary)
                .cursor(Cursor.Pointer)
                .onClick { onEvent(CartUiEvent.IncrementQuantity(item.product)) },
            contentAlignment = Alignment.Center
        ) {
            FaPlus(
                modifier = Modifier
                    .fontSize(if (isCompact) 0.7.cssRem else 0.9.cssRem)
                    .color(rgb(255, 255, 255))
            )
        }
    }
}

@Composable
private fun CartSummary(
    state: CartUiState,
    onEvent: (CartUiEvent) -> Unit,
    ctx: com.varabyte.kobweb.core.PageContext
) {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(1.5.cssRem)
            .borderRadius(0.75.cssRem)
            .backgroundColor(rgba(0, 0, 0, 0.03))
            .gap(1.cssRem)
    ) {
        SpanText(
            "Resumen del Pedido",
            Modifier
                .fontSize(1.5.cssRem)
                .fontWeight(FontWeight.Bold)
                .color(ColorMode.current.toSitePalette().brand.primary)
        )

        // Summary Rows
        Row(
            modifier = Modifier.fillMaxWidth().justifyContent(JustifyContent.SpaceBetween)
        ) {
            SpanText(
                "Total de Artculos:",
                Modifier
                    .fontSize(1.cssRem)
                    .color(rgba(100, 100, 100, 0.9))
            )
            SpanText(
                "${state.totalItems}",
                Modifier
                    .fontSize(1.cssRem)
                    .fontWeight(FontWeight.SemiBold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
        }

        Row(
            modifier = Modifier.fillMaxWidth().justifyContent(JustifyContent.SpaceBetween)
        ) {
            SpanText(
                "Total:",
                Modifier
                    .fontSize(1.5.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
            SpanText(
                "$${state.totalPrice.toFixed(2)}",
                Modifier
                    .fontSize(1.8.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.accent)
            )
        }

        // Action Buttons
        Column(
            modifier = Modifier.fillMaxWidth().gap(0.75.cssRem),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            // Checkout Button
            Box(
                modifier = Modifier
                    .fillMaxWidth()
                    .padding(1.2.cssRem, 0.75.cssRem)
                    .borderRadius(0.75.cssRem)
                    .backgroundColor(ColorMode.current.toSitePalette().brand.accent)
                    .cursor(Cursor.Pointer)
                    .onClick {
                        // TODO: Navigate to checkout page
                        println("Proceeding to checkout...")
                    },
                contentAlignment = Alignment.Center
            ) {
                Row(
                    modifier = Modifier.gap(0.75.cssRem),
                    verticalAlignment = Alignment.CenterVertically
                ) {
                    FaCreditCard(
                        modifier = Modifier
                            .fontSize(1.3.cssRem)
                            .color(rgb(255, 255, 255))
                    )
                    SpanText(
                        "Proceder al Pago",
                        Modifier
                            .fontSize(1.2.cssRem)
                            .fontWeight(FontWeight.Bold)
                            .color(rgb(255, 255, 255))
                    )
                }
            }

            // Clear Cart Button
            Box(
                modifier = Modifier
                    .padding(0.75.cssRem, 0.5.cssRem)
                    .cursor(Cursor.Pointer)
                    .onClick { onEvent(CartUiEvent.ClearCart) }
            ) {
                SpanText(
                    "Vaciar Carrito",
                    Modifier
                        .fontSize(0.95.cssRem)
                        .fontWeight(FontWeight.Medium)
                        .color(rgba(220, 53, 69, 0.8))
                )
            }
        }
    }
}